export default {
	props: {
		// tab背景颜色
		bgColor: {
			type: String,
			default () {
				return '#F73657';
			}
		},
		// tab文字默认颜色
		txtColor: {
			type: String,
			default () {
				return '#FFFFFF';
			}
		},
		// tab激活状态文字颜色
		txtActiveColor: {
			type: String,
			default () {
				return '#FFFFFF';
			}
		},
		// 是否显示underLine
		showUnderLine: {
			type: Boolean,
			default () {
				return true;
			}
		},
		// underLine的颜色
		underLineColor: {
			type: String,
			default () {
				return '#FFFFFF';
			}
		},
		// tab项为数组
		tabData: {
			type: Array,
			default () {
				return ['demo1', 'demo2', 'demo3'];
			}
		}
	},
	data() {
		return {
			seletedIndex: 0
		};
	},
	methods: {
		clickTab(item, index) {
			this.seletedIndex = index;
			this.$emit('seletedTab', index);
		}
	}
}
